import Component from '../react/component';
import { setAttributes } from './dom';

//ËôöÊãüDOM => ÂÆûÈôÖDOM
//{return:HTMLDom}
function _render(vNode) {
    if (vNode === undefined || vNode === null ||typeof vNode === "boolean") {
        vNode = "";
    }
    //tag‰∏∫Â≠óÁ¨¶‰∏≤ÂàôÊ≠§ËôöÊãüDOM‰∏∫ÂéüÁîüHTMLÁªÑ‰ª∂
    if (typeof vNode === "string" || typeof vNode === "number") {
        let textNode = document.createTextNode(vNode);
        return textNode;
    }
    //component.base‰øùÂ≠òÁöÑÊòØÁªÑ‰ª∂ÁöÑdomÂØπË±°
    //tag‰∏∫ÂáΩÊï∞ÂàôÊ≠§ËôöÊãüDOM‰∏∫Ëá™ÈÄ†ÁªÑ‰ª∂
    if(typeof vNode.tag === "function") {
        let component = createComponent(vNode.tag,vNode.attrs);
        setComponentProps(component,vNode.attrs);
        return component.base;
    }
    let dom = document.createElement(vNode.tag);
    //ÂØπ‰∫éÂéüÁîüÁªÑ‰ª∂Áõ¥Êé•Áî®setAttributesÊääÊâÄÊúâÂ±ûÊÄßÂÖ®ÈÉ®ËÆæÁΩÆÂ•Ω
    //Ëã•ËôöÊãüDOMÊúâattrsÂàôÂ∞ÜÊØè‰∏™attrsÂ±ûÊÄßÈÉΩÁªëÂà∞ÂÆûÈôÖDOM‰∏ä
    if (vNode.attrs) {
        Object.keys(vNode.attrs).forEach(key => {
            setAttributes(dom,key,vNode.attrs[key]);    
        });
    }
    //ÊØè‰∏™Ëá™ÂÆö‰πâÁªÑ‰ª∂ÊúÄÂ§ñÂ±Ç‰∏∫<div></div>ÊâÄ‰ª•ÂèØ‰ª•ËøõË°åÈÄíÂΩíÊ∏≤Êüì
    //ÈÄíÂΩíÊ∏≤ÊüìÂ≠êËäÇÁÇπ
    vNode.children.forEach(child => render(child,dom));
    return dom;
}


//ËøîÂõûÁ±ªÁªÑ‰ª∂ÊàñÂáΩÊï∞ÂûãÁªÑ‰ª∂Êâ©Â±ï‰∏∫Á±ªÁªÑ‰ª∂ÁöÑÂÆû‰æã
//{return:React.Component}
function createComponent(component,props) {
    let instance;
    //Ëã•‰∏∫Á±ªÁªÑ‰ª∂ÂàôÂèØ‰ª•Áõ¥Êé•ËøîÂõûÂÆû‰æã,‰∏îÊúârenderÊñπÊ≥ï
    if(component.prototype && component.prototype.render) {
        instance = new component(props);
    } else {
        //ÂáΩÊï∞ÁªÑ‰ª∂ÂàôÊâ©Â±ïÂÆö‰πâ‰∏∫Á±ªÁªÑ‰ª∂
        //Â¢ûÂä†renderÊñπÊ≥ï
        instance = new Component( props );
        instance.constructor = component;
        instance.render = function() {
            //this -> instance
            //ËøîÂõûvNode(tag,attr,children)
            return this.constructor(props);
        }
    }
    //ËøîÂõûinstance(base,renderÊñπÊ≥ï,propsÂ±ûÊÄß)
    console.log(instance);
    return instance;
}

//Êõ¥Êñ∞props
//ÂÆûÁé∞componentWillMountÂíåcomponentWillReceivePropsÊñπÊ≥ï
function setComponentProps(component,props) {
    //ÁîüÊàêÂÆûÈôÖDOMÂâçËß¶ÂèëcomponentWilMountÊñπÊ≥ï
    if(!component.base) {
        if (component.componentWillMount) {
            component.componentWillMount();
        }
    }else if (component.componentWillReceiveProps) {
        component.componentWillReceiveProps(props);
    }
    component.props = props;
    renderComponent(component);
}

//Áî®‰∫éÊ∏≤ÊüìÁªÑ‰ª∂,ËæìÂÖ•‰∏∫Á±ªÁªÑ‰ª∂
//component.base‰øùÂ≠òÁöÑÊòØÂÆûÈôÖDOMÔºå
//ÂèçËøáÊù•base._component‰øùÂ≠òÁöÑÊòØdomÂØπË±°ÊâÄÂØπÂ∫îÁöÑinstanceÂÆû‰æãÔºå‰∏∫‰∫ÜÊää‰ªñ‰ª¨ÂÖ≥ËÅîËµ∑Êù•
//ÂèØÂÆûÁé∞componentWillUpdate,componentDidUpdate,componentDidMount
export function renderComponent(component) {
    let base;
    let render = component.render();//ËøîÂõûinstance,ËôöÊãüDOM
    //ÊúâÂÆûÈôÖDOMÊó∂ÊâçÂèØÂá∫ÂèëcomponentWillUpdate
    if (component.base && component.componentWillUpdate) {
        component.componentWillUpdate();
    }
    base = _render(render);//Áî®ËôöÊãüdomÁîüÊàêÂÆûÈôÖDOM
    //Êõ¥Êñ∞ÂÆûÈôÖDOM‰πãÂêéËß¶ÂèëcomponentDidXXXX‰∫ã‰ª∂
    if(component.base) {
        if (component.componentDidUpdate) {
            component.componentDidUpdate();
        }
    }else if (component.componentDidMount) {
        component.componentDidMount();    
    }
    if (component.base && component.base.parentNode) {
        //Â∞Ücomponent.baseÊõøÊç¢Êàêbase
        //Êõ¥Êñ∞Ê≠§ËäÇÁÇπ‰∏∫ÈáçÊñ∞Ê∏≤ÊüìÂêéÁöÑËäÇÁÇπbase,Âπ∂ÈáçÊñ∞ÁªëÂÆö
        component.base.parentNode.replaceChild(base,component.base);
    }
    component.base = base;
    base._component = component;
}

function unmountComponent(component) {
    if (component.componentWillUnmount) {
        component.componentWillUnmount();
    }
    removeNode(component.base);
}

//Â∞Ü_renderÂáΩÊï∞ÂêéËøîÂõûÁöÑÂÆûÈôÖDOMÂÜçÁªëÂà∞container DOM‰∏ãÈù¢
//Âπ∂ËøîÂõûÂÆûÈôÖDOM containerËäÇÁÇπ
export function render(vNode,container) {
    //mount rendered dom to container
    return container.appendChild(_render(vNode));
}