import { renderComponent } from '../react-dom/render';

//React.Component
class Component{
    constructor(props = {}) {
        this.state = {};
        this.props = props;
    }
    setState(stateChange) {
        //Â∞ÜstateChangeÁöÑÂ±ûÊÄßÂÖ®ÈÉ®Êã∑Ë¥ùÂà∞this.state‰∏äÈù¢
        Object.assign(this.state,stateChange);
        renderComponent(this);
    }
}

export default Component;